import React from 'react';
import { Modal } from '../Modal';
import { Paragraph } from '../Paragraph';
import { CustomButton } from '../CustomButton';

export const ClickOnMapPopup = ({ show, onConfirmPlane, onConfirmShip, onCancel }) => {
  return (
    <Modal show={show}>
      <Paragraph text={'     ?'} />
      {onConfirmPlane && <CustomButton onClick={onConfirmPlane} text="" />}
      {onConfirmShip && <CustomButton onClick={onConfirmShip} text="" />}
      <CustomButton onClick={onCancel} text="" />
    </Modal>
  );
};
